.toolbar {
  top: 0;
  left: 0;
  width: 100%;
  background: #1A2980;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #26D0CE, #1A2980);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, #26D0CE, #1A2980); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.toolbar__navigation {
  display: flex;
  height: 100%;
  align-items: center;
  padding: 0 1rem;
}

.toolbar__logo {
  margin-left: 1rem;
  padding-bottom: 1px;
  padding-top: 10px;
}
.toolbar__logo img {
  color: #26D0CE;
  text-decoration: none;
  font-size: 1.5rem;
}
@media only screen and (min-width: 600px) {
  
}

.spacer {
  flex: 1;
}

.toolbar_navigation-items ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.toolbar_navigation-items li {
  padding: 0 0.5rem;
}

.toolbar_navigation-items a {
  color: white;
  text-decoration: none;
}

.toolbar_navigation-items a:hover,
.toolbar_navigation-items a:active {
  color: #373a47;
}

@media (max-width: 768px) {
  .toolbar_navigation-items {
    display: none;
  }
}

@media (min-width: 769px) {
  .toolbar__toggle-button {
    display: none;
  }

  .toolbar__logo {
    margin-left: 0;
  }
}

nav {
  text-align: right;
  width: 100%;
  max-height: 100%;
}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  margin-right: 2rem;
}

nav li {
  display: inline-block;
}

nav a {
  text-decoration: none;
  font-weight: bold;
  padding: 16px 32px;
}

nav p {
  padding-right: 5px;
}

.nav-link {
  display: inline-flex;
  height: fit-content;
  color: #373a47;
  margin-top: 13px;
}

.nav-btn {
 
  padding-bottom: 0;
  margin-bottom: 15px;
}
