.join-page {
  background: #1A2980;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to , #26D0CE, #1A2980);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, #26D0CE, #1A2980); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  height: 100vh;
}

.errorMessage {
  color: red;
  font-size: 0.75em;
  display: relative;
  background-color: aliceblue;
  padding: 3px;
  border-radius: 3px;
}